html {
  font-size: 20px;
}

.breed-select {
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 8px 12px;
  font-size: 20px;
  display: block;
}

.loader {
  color: #4169e1;
  display: none;
  position: fixed;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader-icon {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #4169e1;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  display: none;
  position: fixed;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.cat-name {
  font-size: 1.2em;
}

.cat-info {
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin-top: 20px;
  padding: 20px;
  display: none;
}

.cat-image {
  max-width: 50%;
  height: auto;
  margin-bottom: 10px;
}

.cat-info p {
  margin: 5px 0;
}

.error {
  color: red;
  display: none;
}

